.workspace {
  padding: 24px 48px;
  height: 100%;

  &__time {
    color: #888888;
    text-align: center;
    margin-bottom: 12px;
  }

  &__editor {
    width: 100%;
    border: none;
    height: 80%;
    font-size: 16px;
    background: transparent;
    outline: none;
    cursor: text;
  }
}
